import JsBridge from './JsBridge'
import KpgUserModule from './KpgUserModule'


function getUrlKey(name) {
    return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href) || [, ""])[1].replace(/\+/g, '%20')) || null
}
export default {
    install(Vue,Store) {
        Store.registerModule('kpg_user', KpgUserModule);
        Vue.prototype.$JsBridge = JsBridge;
        let refreshToken = getUrlKey('refreshToken');
        Store.dispatch('kpg_user/getUser',refreshToken);
        setInterval(()=>{
            Store.dispatch('kpg_user/getUser',refreshToken);
        },3600000)
    }
}