import API from './api'
export default{
    namespaced: true,
    state: {
        refreshToken: null,
        accessToken: null,
        expiresIn: null,
        userId:null,
        name:null,
        photo:null,
        sex:null,
        birthday:null,
        phone:null,
        weixin:null,
        wxname:null,
        roleIds:[],
        orgId:null,
        orgName:null,
        schIds:[],
        schNames:[]
    },
    mutations: {
        updateUser(state,user) {
            state.userId = user.userId;
            state.name = user.name;
            state.photo = user.photo;
            state.sex = user.sex;
            state.birthday = user.birthday;
            state.phone = user.phone;
            state.weixin = user.weixin;
            state.wxname = user.wxname;
            state.roleIds = user.roleIds;
            state.orgId = user.orgId;
            state.orgName = user.orgName;
            state.schIds = user.schIds;
            state.schNames = user.schNames;
        },
        updateToken(state,token){
            state.refreshToken = token.refreshToken;
            state.accessToken = token.accessToken;
            state.expiresIn = token.expiresIn;
        }
    },
    actions: {
        async authRefresh(context,refreshToken){
            let res = await API.refreshToken(refreshToken);
            let token = res.data;
            context.commit('updateToken',token);
            return token.accessToken;
        },
        async getUser(context,refreshToken){
            let accessToken = await context.dispatch('authRefresh',refreshToken);
            let res = await API.getUser(accessToken);
            let user = res.data;
            context.commit('updateUser',user);
        }
    }
}